import { config } from '@/utils/api.js';
export default {
	name: 'cus-footer-bar',
	props: {
		cusFooterBarIsShow: {
			type: Boolean,
			default: false
		},
		isOpenAdaptation: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			staticUrl: config.staticUrl,
			footerbarAni: ''
		};
	},
	methods: {
		jumpFun(type) {
			if (type == 1) {
				uni.switchTab({
					url: '/pages/index/index'
				});
			} else if (type == 2) {
				uni.switchTab({
					url: '/pages/openScan/openScan'
				});
			} else if (type == 3) {
				uni.switchTab({
					url: '/pages/personalCenter/personalCenter'
				});
			}
		},
		// 开始动画
		startAnimation() {
			console.log(3);
			const that = this;
			const footerbarAnimation = wx.createAnimation({
				duration: 1000,
				timingFunction: 'ease',
				delay: 1500
			});
			footerbarAnimation.opacity(1).step();
			that.footerbarAni = footerbarAnimation.export();
		},
		isStartAnimationFun(e) {
			const that = this;
			this.startAnimation();
		}
	}
};
